.text-overflow {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.close-Icon {
  color: #e8e8e8;
  font-size: 12px;
  -webkit-transition: color 0.3s;
  transition: color 0.3s;
  cursor: pointer;
}
.close-Icon:hover {
  color: rgba(0, 0, 0, 0.335);
}
.RA-setting-contentControl .RA-setting-settingItem,
.RA-setting-layoutControl .RA-setting-settingItem {
  padding: 16px 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.RA-setting-contentControl .RA-setting-settingItem .RA-setting-settingItem-Label,
.RA-setting-layoutControl .RA-setting-settingItem .RA-setting-settingItem-Label {
  color: var(--text-color);
  font-weight: 400;
  font-size: 14px;
}
.RA-setting-contentControl .RA-setting-settingItem .RA-setting-settingAction,
.RA-setting-layoutControl .RA-setting-settingItem .RA-setting-settingAction {
  margin: 0;
  width: 80px !important;
}
